/* Career Page Styles */

/* Main Container - Desktop */
@media (min-width: 1024px) {
  /* Override Material-UI Container maxWidth="lg" for desktop */
  .MuiContainer-maxWidthLg {
    max-width: 90% !important;
    width: 90% !important;
    padding-left: 20px !important;
    padding-right: 20px !important;
  }
  
  .career-container {
    width: 90%;
    max-width: 1400px;
    margin: 0 auto;
    padding: 0 20px;
  }
  
  .section-container {
    width: 90%;
    max-width: 1400px;
    margin: 0 auto;
    padding: 0 20px;
  }
  
  /* Women empowerment container - use full container width */
  .women-empowerment-container {
    width: 100% !important;
    max-width: none !important;
  }
}

/* Hero Section */
.career-hero {
  position: relative;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: white;
  /* clip-path: ellipse(100% 85% at 50% 0%); */
  overflow: hidden;
  
}

.hero-content {
  position: relative;
  z-index: 2;
  text-align: center;
  color: white;
  padding: 20px;
  /* background: rgba(0, 0, 0, 0.4); */
  border-radius: 16px;
  max-width: 800px;
  margin: 0 auto;
}

.hero-title {
  font-size: 1.5rem !important;
  font-weight: 600 !important;
  margin: 10px 0 !important;
  line-height: 1.2 !important;
  color: white !important;
  text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.8) !important;
}

.hero-subtitle {
  font-size: 2.8rem !important;
  font-weight: 700 !important;
  margin-bottom: 20px !important;
  color: white !important;
  text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.8) !important;
}

.job-search-form {
  background: #f8f9fa;
  padding: 1rem;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  max-width: 600px;
  margin: 20px auto 0;
  display: flex;
  gap: 8px;
  justify-content: center;
  align-items: center;
  opacity: 0.8;
}

.search-button {
  background: linear-gradient(45deg, #FF6B35, #F7931E) !important;
  color: white !important;
  font-weight: 600 !important;
  padding: 1rem 2rem !important;
  border-radius: 8px !important;
  text-transform: none !important;
  font-size: 1.1rem !important;
  box-shadow: 0 4px 15px rgba(255, 107, 53, 0.3) !important;
  transition: all 0.3s ease !important;
}

.search-button:hover {
  transform: translateY(-2px) !important;
  box-shadow: 0 6px 20px rgba(255, 107, 53, 0.4) !important;
}

/* What we do Section */
.what-we-do-section {
  background: linear-gradient(135deg, #E8F4F8, #F0F8FA);
  padding: 2rem 0;
}

.section-title {
  font-size: 2.5rem !important;
  font-weight: 700 !important;
  color: "white" !important;
  text-align: center;
  margin-bottom: 1rem !important;
}

.section-description {
  font-size: 1.1rem !important;
  color: #5A6C7D !important;
  line-height: 1.6 !important;
  margin-bottom: 1.5rem !important;
}

.service-card {
  background: white !important;
  border-radius: 16px !important;
  border-top-right-radius: 0 !important;
  border-bottom-left-radius: 0 !important;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1) !important;
  transition: all 0.3s ease !important;
  height: 100% !important;
  border: none !important;
}

.service-card:hover {
  transform: translateY(-8px) !important;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.15) !important;
}

.card-title {
  font-weight: 600 !important;
  color: #2C3E50 !important;
  margin-bottom: 1rem !important;
}

.card-description {
  color: #5A6C7D !important;
  line-height: 1.6 !important;
}

/* Why choose section */
.why-choose-section {
  background: white;
  padding: 2rem 0;
}

.why-choose-title {
  font-size: 2.5rem !important;
  font-weight: 700 !important;
  color: #2C3E50 !important;
  line-height: 1.2 !important;
  margin-bottom: 1rem !important;
}

.one-window-text {
  font-size: 2.2rem !important;
  font-weight: 500 !important;
  color: #2C3E50 !important;
}

.why-choose-description {
  font-size: 1.1rem !important;
  color: #7F8C8D !important;
  line-height: 1.6 !important;
  margin-bottom: 1rem !important;
}

.benefits-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.benefit-item {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
}

.benefit-number {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: 2px solid #E0E0E0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  font-weight: 700;
  color: #495052;
  background: #EFF4F7;
  flex-shrink: 0;
  font-family: 'Arial', sans-serif;
}

.benefit-content {
  flex: 1;
}

.benefit-title {
  font-size: 1.25rem !important;
  font-weight: 700 !important;
  color: #2C3E50 !important;
  margin-bottom: 0.75rem !important;
  line-height: 1.3 !important;
}

.benefit-description {
  font-size: 0.95rem !important;
  color: #7F8C8D !important;
  line-height: 1.6 !important;
  margin: 0 !important;
}

.illustration-container {
  position: relative;
  width: 600px;
  height: 500px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  margin: 0 auto;
}

.illustration-placeholder {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 20px;
  border: none;
  overflow: hidden;
}

.illustration-avatar {
  position: absolute !important;
  width: 120px !important;
  height: 120px !important;
  background: linear-gradient(45deg, #70A6B2, #5A8A96) !important;
  color: white !important;
  box-shadow: 0 8px 25px rgba(112, 166, 178, 0.3) !important;
}

.illustration-1 {
  top: 20%;
  left: 10%;
  animation: float 3s ease-in-out infinite;
}

.illustration-2 {
  top: 50%;
  right: 20%;
  animation: float 3s ease-in-out infinite 1s;
}

.illustration-3 {
  bottom: 20%;
  left: 30%;
  animation: float 3s ease-in-out infinite 2s;
}

@keyframes float {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-20px); }
}

/* Passion section */
.passion-section {
  background: white;
  padding: 2rem 0;
}

.passion-illustration-container {
  position: relative;
  height: 500px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
}

.passion-illustration-placeholder {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: transparent;
  border-radius: 20px;
  border: none;
  overflow: hidden;
}

.passion-content {
  padding-left: 1rem;
}

.passion-title {
  font-size: 2.5rem !important;
  font-weight: 700 !important;
  color: #2C3E50 !important;
  line-height: 1.2 !important;
  margin-bottom: 0.5rem !important;
}

.passion-description {
  font-size: 1.1rem !important;
  color: #7F8C8D !important;
  line-height: 1.6 !important;
  margin-bottom: 1rem !important;
}

.passion-benefits {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.passion-benefit-item {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
}

.passion-benefit-icon {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.real-world-icon {
  background: #FFB366;
}

.networking-icon {
  background: #B19CD9;
}

.career-icon {
  background: #4ECDC4;
}

.passion-benefit-content {
  flex: 1;
}

.passion-benefit-title {
  font-size: 1.25rem !important;
  font-weight: 700 !important;
  color: #2C3E50 !important;
  margin-bottom: 0.5rem !important;
  line-height: 1.3 !important;
}

.passion-benefit-description {
  font-size: 0.95rem !important;
  color: #7F8C8D !important;
  line-height: 1.6 !important;
  margin: 0 !important;
}

.internship-button {
  background: #F48438 !important;
  color: white !important;
  font-weight: 600 !important;
  padding: 1rem 2rem !important;
  border-radius: 12px !important;
  text-transform: none !important;
  font-size: 1.1rem !important;
  box-shadow: 0 4px 15px rgba(74, 74, 138, 0.3) !important;
  transition: all 0.3s ease !important;
  border: none !important;
}

.internship-button:hover {
  background: #F48438!important;
  transform: translateY(-2px) !important;
  /* box-shadow: 0 6px 20px rgba(74, 74, 138, 0.4) !important; */
}

/* Benefits section */
.benefits-section {
  background: white;
  padding: 2rem 0;
}

.benefits-section-title {
  font-size: 2.5rem !important;
  font-weight: 700 !important;
  color: #2C3E50 !important;
  text-align: center !important;
  margin-bottom: 0.5rem !important;
}

.benefits-section-description {
  font-size: 1.1rem !important;
  color: #2C3E50 !important;
  text-align: center !important;
  line-height: 1.6 !important;
  margin-bottom: 1rem !important;
}

.benefit-image-card {
  width: 100%;
  height: 300px;
  border-radius: 20px;
  overflow: hidden;
  position: relative;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.12);
  transition: all 0.3s ease;
}

.benefit-image-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.15);
}

.benefit-image-container {
  position: relative;
  width: 100%;
  height: 100%;
}

.benefit-image-placeholder {
  width: 100%;
  height: 100%;
  background: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
}

.benefit-image-overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(transparent, rgba(32, 178, 170, 0.8));
  padding: 2rem 1.5rem 1.5rem;
  display: flex;
  align-items: flex-end;
}

.benefit-image-label {
  color: white !important;
  font-weight: 700 !important;
  font-size: 1.25rem !important;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3) !important;
  margin: 0 !important;
}

/* Women empowerment section */
.women-empowerment-section {
  background: white;
  padding: 1rem 0;
}

.women-empowerment-container {
  background: #F5EEE4;
  border-radius: 50px;
  padding: 2rem 1rem;
  margin: 1rem 0;
  max-width: 1400px;
  /* margin-left: auto;
  margin-right: auto; */
  width: 100%;
}

.women-illustration-container {
  position: relative;
  height: 300px;
  display: flex;
  /* align-items: center;
  justify-content: center; */
  padding: 0.5rem;
}

.women-illustration-placeholder {
  width: 100%;
  height: 100%;
  display: flex;
  /* align-items: center;
  justify-content: center; */
  background: transparent;
  border-radius: 25px;
  border: none;
  overflow: hidden;
}

/* .women-empowerment-content {
  padding-left: 1rem;
} */

.women-empowerment-title {
  font-size: 2.3rem !important;
  font-weight: 700 !important;
  color: #2C3E50 !important;
  line-height: 1.2 !important;
  margin-bottom: 1.5rem !important;
}

.women-empowerment-description {
  font-size: 1rem !important;
  color: #2C3E50 !important;
  line-height: 1.6 !important;
  margin: 0 !important;
}

/* Join team section */
.join-team-section {
  background: white;
  padding: 2rem 0;
}

.join-team-content {
  padding-right: 0.5rem;
  max-width: 400px;
}

.join-team-title {
  font-size: 2.2rem !important;
  font-weight: 700 !important;
  color: #2C3E50 !important;
  margin-bottom: 0.75rem !important;
  line-height: 1.2 !important;
}

.join-team-subtitle {
  font-size: 1.1rem !important;
  font-weight: 400 !important;
  color: #7F8C8D !important;
  margin-bottom: 0 !important;
}

.job-card {
  background: #F8F9FA !important;
  border-radius: 8px !important;
  box-shadow: none !important;
  border: 1px solid #E9ECEF !important;
  transition: all 0.3s ease !important;
  height: 100% !important;
}

.job-card:hover {
  transform: translateY(-2px) !important;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.08) !important;
}

.job-card-content {
  padding: 1.25rem !important;
}

.job-posted {
  color: #6C757D !important;
  font-weight: 400 !important;
  font-size: 0.875rem !important;
  margin-bottom: 0.75rem !important;
}

.job-title {
  font-weight: 700 !important;
  color: #2C3E50 !important;
  font-size: 1.25rem !important;
  margin-bottom: 1rem !important;
}

.job-details {
  display: flex;
  flex-direction: column;
  gap: 0.4rem;
  margin-top: 1rem !important;
  margin-bottom: 1rem !important;
}

.job-detail-item {
  display: flex;
  align-items: center;
}

.job-detail-text {
  color: #6C757D !important;
  font-size: 0.8rem !important;
  font-weight: 400 !important;
}

.job-responsibilities {
  margin-top: 1rem !important;
}

.job-responsibilities .MuiListItem-root {
  padding: 0.25rem 0 !important;
}

.job-responsibilities .MuiListItemText-root {
  margin: 0 !important;
}

/* Utility classes */
.text-center {
  text-align: center !important;
}

/* Responsive Design */
@media (max-width: 768px) {
  .hero-title {
    font-size: 2rem !important;
    color: white !important;
    text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.8) !important;
  }
  
  .hero-subtitle {
    font-size: 1.5rem !important;
    color: white !important;
    text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.8) !important;
  }
  
  .section-title {
    font-size: 2rem !important;
  }
  
  .job-search-form {
    padding: 1.5rem;
  }
  
  .why-choose-title {
    font-size: 2rem !important;
  }
  
  .one-window-text {
    font-size: 2.2rem !important;
  }
  
  .benefit-item {
    flex-direction: column;
    text-align: center;
    gap: 1rem;
  }
  
  .benefit-number {
    width: 50px;
    height: 50px;
    font-size: 1.2rem;
  }
  
  .illustration-container {
    height: 300px;
    margin-top: 2rem;
  }
  
  .passion-illustration-container {
    height: 300px;
    margin-bottom: 2rem;
  }
  
  .passion-content {
    padding-left: 0;
  }
  
  .passion-title {
    font-size: 2rem !important;
  }
  
  .passion-benefit-item {
    flex-direction: column;
    text-align: center;
    gap: 1rem;
  }
  
  .passion-benefit-icon {
    width: 45px;
    height: 45px;
  }
  
  .benefits-section-title {
    font-size: 2rem !important;
  }
  
  .benefit-image-card {
    height: 250px;
  }
  
  .women-empowerment-container {
    padding: 2rem 1rem;
    width: 90%;
  }
  
  .women-illustration-container {
    height: 300px;
    margin-bottom: 2rem;
  }
  
  .women-empowerment-content {
    padding-left: 0;
  }
  
  .women-empowerment-title {
    font-size: 2rem !important;
  }
  
  .join-team-section {
    padding: 2rem 0;
  }
  
  .join-team-content {
    padding-right: 0;
    margin-bottom: 2rem;
    max-width: none;
  }
  
  .join-team-title {
    font-size: 1.8rem !important;
  }
  
  .join-team-subtitle {
    font-size: 1rem !important;
  }
  
  .illustration-avatar {
    width: 80px !important;
    height: 80px !important;
  }
}

@media (max-width: 480px) {
  .hero-title {
    font-size: 1.8rem !important;
    color: white !important;
    text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.8) !important;
  }
  
  .hero-subtitle {
    font-size: 1.3rem !important;
    color: white !important;
    text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.8) !important;
  }
  
  .section-title {
    font-size: 1.8rem !important;
  }
  
  .why-choose-title {
    font-size: 1.8rem !important;
  }
  
  .one-window-text {
    font-size: 2rem !important;
  }
  
  .benefit-number {
    width: 45px;
    height: 45px;
    font-size: 1rem;
  }
  
  .passion-title {
    font-size: 1.8rem !important;
  }
  
  .passion-benefit-icon {
    width: 40px;
    height: 40px;
  }
  
  .benefits-section-title {
    font-size: 1.8rem !important;
  }
  
  .benefit-image-card {
    height: 200px;
  }
  
  .benefit-image-label {
    font-size: 1.1rem !important;
  }
  
  .women-empowerment-container {
    padding: 1.5rem 0.5rem;
    width: 85%;
  }
  
  .women-illustration-container {
    height: 250px;
  }
  
  .women-empowerment-title {
    font-size: 1.8rem !important;
  }
  
  .join-team-section {
    padding: 1.5rem 0;
  }
  
  .join-team-content {
    padding-right: 0;
    margin-bottom: 1.5rem;
    max-width: none;
  }
  
  .join-team-title {
    font-size: 1.6rem !important;
  }
  
  .join-team-subtitle {
    font-size: 0.9rem !important;
  }
  
  .job-card-content {
    padding: 1.25rem !important;
  }
  
  .job-search-form {
    padding: 1rem;
  }
}

